  margin-right: 42px;
  position: relative;
  padding-top: 142px;
}

#card-video video {
  width: 100%;
  height: 100%;
  position: absolute;
  -webkit-backdrop-filter: blur(7px);
          backdrop-filter: blur(7px);
  top: 0;
  left: 0;
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) )
}

#card-avatar {
  width: 254px;
  height: 50px;
  border-bottom-left-radius: 12px;
